body{
    margin:0;
    padding:0;
    background-color: #0E1215;
    color:#F2F6F7;
    font-family: 'Montserrat', sans-serif;
    
}

.welcome{
    width:100%;
    height: 783px;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('./images/welcome.png');
}
::-webkit-scrollbar {
    width:3px;
    height: 3px;
}
     
::-webkit-scrollbar-track {
    background:transparent;
}
     
    
::-webkit-scrollbar-thumb {
    background: #EFEEEE;
}
header{
    background-color: rgba(0, 14, 18, 0.363);
}

.degrade{
margin-top:-200px;
padding-top:200px;

background:linear-gradient(0deg,#0E1215,transparent);
}

.alert{
    width:100%;
    height:100%;
    position: fixed;
    background-color:rgba(0, 14, 18, 0.884);
    z-index: 99;
}

main{height:100%}
.tittle{
    font-family: 'Cantata one',sans-serif;
    font-size: 11.7em;
}
.bottom-margin{
    margin-bottom: -70px;
} 
.top-margin{
    margin-top: -70px;
}   
.main-light{
    font-weight: bold;
    font-size: 2em;
}
nav div{
    padding:20px;
}

article{
    font-size: 16px;
    transition: 1s all linear;
    background-color:#000E12;
}
article h4{
    border-left: 3px solid #00799E;
    padding:20px;
    font-size: 16px;
    font-weight: bold;
}
footer{
    margin-top:150px;
}
footer h4{
    font-weight: bold;
    font-size: 22px;
}
footer input,textarea{
    background-color: rgba(0, 121, 158, 0.336);
    border:0px;
    color:#EFEEEE;
    padding:11px;
    
}
textarea{
    width:80%;
    height:200px;
    
}
.hoteis{
    margin-top: 150px;
}
button{    
    border:1px solid #00799E;
    background: transparent;
    color:#EFEEEE;
    padding:10px;
}
.card-text{
    
    transition: .6s  ease-in-out;
    background-color: #14181B;
    padding:20px;
}

.about-c{
    margin-top:-100px;
}
.about__image{
    height:400px;
    width:100%;
    margin-bottom:-50px;
    margin-top:-100px;
    box-shadow: -26px 11px 25px rgba(0, 0, 0, 0.486);
}


a,a:hover,a:active{
    color:#EFEEEE;
    text-decoration: none;
}



.section-bar{
    background-color: rgba(20, 24, 27, 0.61);
}
h6{
    font-family: 'Cantata one',sans-serif;
    font-size: 5em;
    margin-top: -70px;
}
.beach-image{
    background-image: url('./images/bimg.png');
    background-size: cover;
    background-repeat: no-repeat;
}

.about p{
    
    padding:20px;
    border-left: 3px solid #EFEEEE;
}
hr{
    height:3px;
    box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.247);
}
.about-box{
    margin-top:150px;
}
.c-img{
    margin-bottom: -50px;
}

.hoteis article{
    margin-top:-60px;
}
.card-hotel{
    margin-top:7px;
}


@media (max-width: 600px){
    .about__image{
        margin-top: 0;
        margin-bottom: 0;
    }
    
    
    .c-img{
        margin-bottom: 0;
    }
    .about h6{
        font-size: 3em;
        margin-top: 0;
    }
    nav div,article,h4,p{
        font-size: 12px;
    }
    article h4{
        font-size: 10px;
    }
    .welcome{
        height:400px
    }
    .tittle{
       
        
        font-size: 3.2em;
    }
    .main-light{
        font-weight: bold;
        font-size: .8em;
    }
    .bottom-margin{
        margin-bottom: -15px;
    } 
    .top-margin{
        margin-top: -15px;
    } 
}
